

.header{
  padding: 4vh;
  text-align: center;
  font-size: 6vh;
  color: rgb(225, 225, 225);
  text-shadow: 4px 3px 1px #1b4d8a;
  font-weight: bold;
  letter-spacing: 1vh;
}

.list-container{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item-container{
  width: 80%;
  height: 5vh;
  background-color: #1b4d8a;
  border-radius: 12px;
  margin-top: 4vh;
}

.item{
  height: 5vh;
  background-color: #4b7ebd;
  width: 100%;
  border-radius: 12px;
  transform: translateY(-6px);
  transition: all 0.5s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.item:hover{
  transform: scale(1.05);
  height: 6vh;
}

.todo-name{
  width: fit-content;
  color: white;
  font-weight: bold;
  text-indent: 3vh;
  font-size: 20px;
  font-size: 22px;
  text-shadow: 4px 1px 0px #1b4d8a;

}

.check-box{
  margin-right: 3vh;
  color: whitesmoke;
  cursor: pointer;
}

.input-container{
  background-color: whitesmoke;
}